.main {
    max-width: 720px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #1C1C21;
    border: 1px solid rgba(76, 76, 255, 0.2);
    box-sizing: border-box;
    border-radius: 40px;
    z-index: 11;
    position: fixed;
    top: 50%;
    left: 50%;
    transform:translateX(-50%)translateY(-50%)
}

.main__orders {
    align-items: flex-start;
    max-height: 716px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px 40px 0;
    width: 100%;
    box-sizing: border-box;
}

.title {
    margin: 0;
}

.icon {
    cursor: pointer;
}

